@font-face {
  font-family: frontend-boilerplate;
  src: url("../fonts/frontend-boilerplate.eot?v=uxgyfyt");
  src: url("../fonts/frontend-boilerplate.eot?#iefix?v=uxgyfyt") format("embedded-opentype"), url("../fonts/frontend-boilerplate.woff2?v=uxgyfyt") format("woff2"), url("../fonts/frontend-boilerplate.woff?v=uxgyfyt") format("woff"), url("../fonts/frontend-boilerplate.ttf?v=uxgyfyt") format("truetype"), url("../fonts/frontend-boilerplate.svg?v=uxgyfyt") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
i {
  font-style: normal;
  line-height: 1;
  display: inline-block;
}

[class^=icon-],
[class*=" icon-"] {
  font-family: "frontend-boilerplate" !important;
  font-style: normal;
  font-weight: normal !important;
  font-size: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-hamburger::before {
  content: "\ea01";
}

.icon-close::before {
  content: "\ea02";
}

.icon-balloon::before {
  content: "\ea03";
}

.icon-leaf::before {
  content: "\ea04";
}

.icon-home::before {
  content: "\ea05";
}

.icon-shower::before {
  content: "\ea06";
}

.orange {
  background-color: rgb(126, 136, 208);
  font-size: 24px;
}

.banana {
  background-color: rgb(188, 239, 111);
  font-size: 34px;
}